.banner-image {
  background-image: url("../../assets/Home/home.jpg");
  /* background-size: fill; */
  background-attachment: fixed;
  min-height: 100%;
}

.blinking-cursor:after {
  content: "|";
  text-shadow: 0 0 30px rgb(0, 0, 0);
  animation: blink 1.2s step-start infinite;
}

.home-brand-active {
  animation-name: brand-ease-in;
  animation-duration: 0.7s;
  animation-timing-function: ease-in;
  animation-delay: 0.3;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes brand-ease-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.home-brand-inactive {
  animation-name: brand-ease-out;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes brand-ease-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

.about-section {
  display: block;
  overflow: auto;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.9989);
  background-size: contain;
  background-repeat: no-repeat;
}

.resume-button {
  color: white;
  text-decoration: none;
}

.resume-button:hover {
  color: white;
  animation-name: underline-resume-button;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes underline-resume-button{
  0% {
    text-decoration: underline solid 5px rgb(0, 0, 0);
  }

  100% {
    text-decoration: underline solid 5px white;
  }
}


@media only screen and (min-width: 2160px) {
  .home-text {
    line-height: 5.4rem;
    letter-spacing: -0.03em;
    font: normal normal normal 180px/1.4em classic;
    text-shadow: 0 0 30px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }

  .about-container {
    margin-top: 18%;
    scale: 1.3;
  }
  
  .about-title {
    font: normal normal normal 30px/35px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
