.timeline-wrapper {
  min-height: 100vh;
  color: #fff;
  position: relative;
  font: Poppins Regular;
}

.section {
  height: 100vh;
  font-size: 40px;
  font-weight: 100;
  background-color: #22a7f0;

  display: flex;
  justify-content: center;
  align-items: center;

  // $colors: white, black, #f64747, #f9690e, #9b59b6, #03c9a9, #ffcc00, black, #f64747,
  //   #f9690e, #9b59b6, #03c9a9, #ffcc00, black;

  $colors: white, black, rgb(10, 35, 104), rgb(23, 24, 124), rgb(8, 9, 59), rgb(1, 2, 70), rgb(2, 14, 27), black, rgb(25, 25, 43),
  rgb(23, 24, 124), rgb(1, 33, 70), rgb(10, 35, 104), rgb(2, 14, 27), black;

  @for $i from 1 through length($colors) {
    &:nth-child(#{$i}) {
      background: nth($colors, $i);
    }
  }
}

.section-text {
    width: 50%;
}

.tnav {
  &__wrapper {
    position: fixed;
    height: 100%;
    padding: 0px;

    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
  }

  & {
    // margin: 0 0 100px 30px;
    margin: 0 0 70px 5px;
    scale: 0.85;
  }

  &__date {
    font-size: 24px;
    transition: all 0.15s ease-out;
  }

  &__event {
    font-weight: 100;
    font-size: 18px;
    width: 300px;
    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: height 0.3s ease-out, opacity 0.2s ease-out;
  }
}

.tnav-li {
  position: relative;
  transition: all 0.3s ease-out;
  margin-bottom: 1em;
  text-decoration: none;
  list-style: none;

  &:after {
    content: "";
    display: block;
    border-left: 2px solid white;
    border-top: 2px solid white;
    height: 250px;
    width: 20px;
    position: absolute;
    left: -30px;
    top: 15px;
  }

  .tnav-a {
    display: block;
    padding: 0;
    color: #fff;
    transition: all 0.15s ease-out;
    text-decoration: none;

    &:hover {
      background-color: transparent;
      padding-left: 1em;
    }

    &:focus {
      background-color: transparent;
    }
  }

  &.active {
    pointer-events: none;
    padding-left: 1em;

    &:after {
      width: 35px;
      height: 400px;
      top: 35px;
    }

    .tnav__date {
      font-size: 35px;
    }

    .tnav__event {
      height: 50px;
      opacity: 1;
      overflow: visible;
    }
  }
}
